html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    min-width: 320px;
    min-height: 100vh;
    background-color: #f9f9f9;
    font-family: 'SF Pro Display', Arial, sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: black;
    line-height: 1;
}


img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    color: inherit;
}

ul,
ol {
    list-style: none;
}

h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
fieldset {
    padding: 0;
    margin: 0;
}


.visually-hidden {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px);
    /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0 !important;
    border: 0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

.clearfix::after {
    content: "";
    display: table;
    clear: both;
}

section {
    padding: 50px 0;
}

button {
    cursor: pointer;
}

.container {
    max-width: 1265px;
    padding: 0 50px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .container {
        padding: 0 30px;
    }
}


@media (max-width: 480px) {
    .container {
        padding: 0 15px;
    }
}
.header {
    min-height: 80px;
    padding: 10px 0;
}

.header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}



.header__burger {
    display: none;
    width: 35px;
    height: 35px;
    background-color: transparent;
    background-image: url('../img/burger.svg');
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
}

.header__sign {
    background-color: transparent;
    border: none;
    background-image: url("../img/lk.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 34px;
    height: 34px;
    border-radius: 5px;
    transition: filter .3s cubic-bezier(0.445, 0.05, 0.55, 0.95);

}

.header__sign2 {
    padding: 0;
    background-color: transparent;
    border: none;
    width: 34px;
    height: 34px;
    border-radius: 5px;
}

.header__sign2-svg {
    width: 34px;
    height: 34px;
    transition: fill .3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.header__sign2-svg:hover {

    fill: #C2B59B;
}


.header__sign:hover {
    filter: drop-shadow(0px 0px 3px black);
}


@media (max-width: 1366px) {

    .header__container {
        flex-wrap: wrap;
        gap: 30px;
    }


    .header__logo {
        margin-right: auto;
    }

}

@media (max-width: 768px) {
    .header__burger {
        display: block;
    }
}

@media (max-width: 768px) {
    .header__container {
        gap: 10px;
    }
    .header__sign {
        font-size: 16px;
    }
}
.navigation {
    display: flex;
    align-items: center;
}

.navigation__list {
    display: flex;
    align-items: center;
    margin-right: 59px;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
}


.navigation__close {
    display: none;
}

.navigation__item {
    margin-right: 60px;
}

.navigation__item:last-child {
    margin-right: 0;
}

.navigation__city {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    margin-right: 94px;
    background-color: transparent;
    border: none;
    
}

.navigation__phone {
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
}

@media (max-width: 1366px) {

    .navigation {
        width: 100%;
        order: 1;
        justify-content: space-between;
    }



    .navigation__list {
        margin-right: 30px;
    }

    .navigation__item {
        margin-right: 30px;
    }

    .navigation__city {
        margin-right: 30px;
    }
}

@media (max-width: 768px) {
    .navigation {
        background-color: #fff;
        max-width: 400px;
        padding: 50px 30px;
        position: absolute;
        flex-direction: column;
        gap: 20px;
        left: -400px;
        top: 0;
    }

    .navigation__open {
        display: flex;

    }

    .navigation__close {
        display: block;
        opacity: 0;
        font-size: 26px;
        background-color: transparent;
        border: none;
        border-radius: 10px;
        position: absolute;
        left: 25px;
        top: 25px;
        width: 50px;
        height: 50px;
        padding: 10px;
    }

    .navigation__list {
        flex-direction: column;
        gap: 20px;
    }

    .navigation__list {
        margin-right: 0;
    }

    .navigation__item {
        margin-right: 0;
    }

    .navigation__city {
        margin-right: 0;
    }
}
.acc__button {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    cursor: pointer;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    border-top: 1px solid transparent;
    border-bottom: 1px solid #cacaca;
    font-size: 20px;
    text-align: left;
    font-weight: 700;
    min-height: 70px;
    margin: 0;
}

.acc:first-child .acc__button {
    border-top: 1px solid #cacaca;
}

.acc:first-child .acc__button:hover {
    border-top: 1px solid #0066ff;
}

.acc__button:focus {
    outline: 1px solid #0066ff;
}

.acc__title {
    text-align: left;
    padding: 10px;
    margin-right: 30px;
}

.acc__text {
    margin: 0;
    font-size: 24px;
    color: #ccc;
    padding: 1em 2.2em;
    border-top: 0;
    /* overflow: auto; */
}

