.city {
    position: absolute;
    top: -100px;
    left: 0;
    width: 100%;
    height: 100px;
    transition: top .5s ease-out;
display: flex;
    background-color: #fff;

}

.city__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
