.modal-order {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}

.modal-order__fieldset {
    align-items: flex-end;
    display: flex;
    flex-direction: column;
    border: none;
}

.modal-order__wrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #f9f9f9;
    width: 600px;
    max-width: 100%;
    padding: 80px 50px;
    box-shadow: 2px 2px 5px rgb(20 29 61 / 15%), -2px -2px 5px rgb(20 29 61 / 15%);
    border-radius: 12px;

