}

.modal-order__submit {
    width: 165px;
    height: 40px;
    border: 1px solid #C2B59B;
    box-sizing: border-box;
    box-shadow: 0px 2px 18px rgb(41 41 41 / 10%);
    border-radius: 6px;
    background-color: #f9f9f9;
    text-transform: uppercase;
