    transition: bottom .5s ease-out;
    background-color: #ccc;
    box-shadow: 0 -1px 10px #ccc;
    display: flex;
    align-items: center;
    padding: 20px 40px;
    justify-content: center;
}

.alert-cookie_no-ready {
    bottom: 0;
}

.alert-cookie__text {
    margin-right: 30px;
}


.slider {
